.post {
  margin: 20px 60px;
  padding: 15px 0;
  border: solid 1pt black;
  border-radius: 10px;
}

.header, .footer, .controls {
  display: flex;
  justify-content: space-between;
}

.header, .footer, .content {
  margin: 15px;
}
/* style post titles */
.header span {
  font-size: 1.5em;
  font-weight: 700;
}

.controls {
  gap: 5px;
}

.fullscreen {
  padding: 30px;
  max-width: 800px;
  margin: 100px auto;
  background: #fff;
  border-radius: 10px;
}
.fullscreen-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}

.content {
  border: 1pt dotted orangered;
  border-radius: 10px;
  padding: 4px;
}

.image {
  width: 30px;
  height: 30px;
}

.downvote {
  transform: rotate(180deg);
}

.footer span {
  display: inline-flex;
  align-items: center;
}

/* Author */
.footer span:first-child {
  font-style: italic;
}
/* Score */
.controls span:first-child {
  font-style: normal;
  border-bottom: 1pt solid orangered;
  border-top: 1pt solid orangered;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
/* Comments */
.controls span:last-child {
  cursor: pointer;
}

.date {
  font-size: .8em;
  color: rgb(79, 79, 79);
  padding-left: 3px;
}

.flexbox-vertical {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}